import Vue from 'vue';
import VueRouter from 'vue-router';
import RadioUI from '../views/RadioUI.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'home',
    component: RadioUI
  }
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
});

export default router;
